
import { defineComponent } from "vue";

export default defineComponent({
  name: "Task",
  props: {
    task: Object,
  },
  data() {
    return {
      contents: "",
      edit: false,
      starStyle: "",
    }
  },
  methods: {
    deleteTask(taskId: number) {
      this.$emit("delete", taskId);
    },
    doEdit() {
      this.edit = true;
    },
    changeColor() {
      this.starStyle = (this.starStyle === "") ? "color: yellow; background: white;" : "";
    }
  }
})
