
import { defineComponent } from "vue";

export default defineComponent({
  name: "addSectionBtn",
  data() {
    return {
      addSec: false,
      sectionTitle: "",
    }
  },
  methods: {
    addSection() {
      if(!this.sectionTitle) return;
      this.addSec = false;
      this.$emit("addSection", this.sectionTitle);
      this.sectionTitle = "";
    },
  }
})
