
import { defineComponent } from 'vue';
import Task from './task.vue';
import TaskNew from './task-new.vue';
import * as data from 'src/data';


export default defineComponent({
  name: 'TaskSection',
  components: {
    Task,
    TaskNew,
  },
  props: {
    section: Object,
  },
  data() {
    return {
      id: 0,
      editSecTitle: false,
      secTitle: "",
      tasks: [] as data.Task[],
    }
  },
  methods: {
    deleteSection(id:number) {
      this.$emit("delete", id)
    },
    changeSection(id: number) {
      if(!this.secTitle) return;
      this.$emit("change", id, this.secTitle);
      this.editSecTitle = false;
      this.secTitle = "";
    },
    addTask(taskTitle:string) {
      this.tasks.push({
        id: this.id,
        title: taskTitle,
        content: "",
      })
      this.id += 1;
    },
    deleteTask(num: number) {
      this.tasks = this.tasks.filter(task => task.id !== num);
    }
  }
});
